@media print {
  h2 {
    .heading{
      margin-bottom: 15px;
    }
  }
  .container {
    width: auto;
  }

  a[href]:after {
    content: none !important;
  }

  .sections-wrapper {
    padding-top: 0;
    padding-bottom: 0;
  }

  .section {
    padding-top: 0;
    padding-bottom: 0;
    margin-bottom: 15px;

    .section-inner {
      padding: 10px;
    }

    .heading {
      margin-bottom: 5px;
    }
  }

  .experience {
    .item {
      margin-bottom: 10px;
    }
  }

  .skills {
    .skillset {

      .item {
        margin-bottom: 10px;
      }
      .level-title {
        font-size: 14px;
        position: relative;
        margin-top: 0;
        margin-bottom: 10;
        .level-label {
          color: lighten(@color, 30%);
          font-size: 12px;
          font-weight: 400;
          font-family: "Lato", arial, sans-serif;
          position: absolute;
          right: 0;
          top: 0;
        }

      }
      .level-bar {
        display: none;
      }
      .level-bar-inner {
        display: none;
      }
    }

  }

  .education {
    .item {
      margin-bottom: 10px;
      &:last-child {
        margin-bottom: 0;
      }
      .title {
        font-size: 14px;
        margin-top: 0;
      }
      .university {
        font-family: "Lato", arial, sans-serif;
        font-size: 11px;
        color: @grey;
        font-weight: 600;
        padding-left: 25px;
        .year {
          color: lighten(@color, 20%);
          font-weight: 500;
        }
      }
    }
  }

  /* Language Section */
  .languages {
    .item {
      margin-bottom: 10px;
      .title {
        color: @color;
      }
      .level {
        color: @grey;
      }
      &:last-child {
        margin-bottom: 0;
      }
      .fa {
        color: lighten(@color-2, 20%);
      }
    }
  }

}
